@font-face {
  font-family: "Proxima Nova";
  src: local("Proxima Nova"), url("../fonts/proximanova-regular-webfont.ttf") format("truetype"), url("../fonts/proximanova-regular-webfont.woff") format("woff"), url("../fonts/proximanova-regular-webfont.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}
body {
  font-family: "Proxima Nova", Arial, sans-serif;
  font-size: 14px;
  line-height: 24px;
  font-weight: normal;
  color: #56534c;
  text-transform: none;
}

.btn {
  display: inline-block;
  box-sizing: border-box;
  cursor: pointer;
  text-align: center;
  vertical-align: top;
  text-decoration: none;
}
.btn:hover {
  opacity: 0.8;
}
.btn:active {
  opacity: 1;
}

input[type="text"],
textarea,
select,
input[type="password"] {
  box-sizing: border-box;
  width: 100%;
  outline: none;
  font-family: "Proxima Nova", Arial, sans-serif;
  font-size: 18px;
  line-height: 20px;
}

label {
  cursor: pointer;
}

button {
  outline: none;
  cursor: pointer;
}

input::-webkit-input-placeholder {
  opacity: 0.8;
}

input::-moz-placeholde {
  opacity: 0.8;
}

body {
  background: white;
  margin: 0px;
  padding: 0px;
  min-width: 1300px;
}

.container {
  width: 1260px;
  padding: 0 20px;
  margin: 0 auto;
}

h1, h2, h3, h4, h5, h6,
p {
  margin: 0;
}

ul {
  padding: 0;
}

